/* ===================== Imports ==================== */
import React from 'react'; // import React to create React component
import './App.css'; // import the CSS file
import myVideo from "./static/film2.webm"
//import greenFlag from './static/img/greenFlag.gif'; // import gif image from the 'src' folder
/* ================================================== */

let array = [];

class App extends React.Component {
  constructor() {
    super();
    this.state={
        scrollIndex: 0,
        scrollPos: 0
    };
  }

  componentDidMount() {
    //document.querySelector('input').addEventListener('change', this.scrollHandle, false);
    window.addEventListener('load', this.videoHandle, false);
    var root = document.querySelector('#root');
    
    root.addEventListener("wheel", event => {
      this.scrollHandle(event.deltaY);
    }, { passive: true});
  }

  componentWillUnmount() {
    //document.querySelector('input').addEventListener('change', this.scrollHandle, false);
    window.addEventListener('load', this.videoHandle, false);
    var root = document.querySelector('#root');
    root.addEventListener("wheel", event => {
      this.scrollHandle(event.deltaY);
    }, { passive: true});
  }

  videoHandle = () => {

    var video = document.createElement('video');
    array = [];
    var canvas = document.createElement('canvas');
    var ctx = canvas.getContext('2d');

    function initCanvas(e) {
      canvas.width = this.videoWidth;
      canvas.height = this.videoHeight;
    }

    function drawFrame (e) {
      this.pause();
      ctx.drawImage(this, 0, 0);
      canvas.toBlob(saveFrame, 'image/jpeg');
      document.getElementById("loader").querySelector("#progress").innerHTML = "LOADING . . . " + ((this.currentTime / this.duration) * 100).toFixed(2) + ' %';
      console.log(((this.currentTime / this.duration) * 100).toFixed(2) + ' %');
      if (this.currentTime < this.duration) {
        this.play();
      }
    }

    function saveFrame(blob) {
      array.push(blob);
    }

    function revokeURL(e) {
      URL.revokeObjectURL(this.src);
    }

    function onend(e) {
      var img;
      // do whatever with the frames
      img = new Image();
      img.crossOrigin="anonymous";
      img.id = "image";
      img.style.height = "75vh";
      //img.style.margin = "12.5vh auto";
      //img.style.position = "absolute";
      img.onload = revokeURL;
      img.src = URL.createObjectURL(array[0]);
      document.getElementById('titleVideo').appendChild(img);
      // we don't need the video's objectURL anymore
      URL.revokeObjectURL(this.src);

      var loader = document.getElementById("loader");

      loader.style.visibility = "hidden";
      loader.style.height = "0";
      loader.style.opacity = "0";

      setTimeout(() => {
        loader.style.zIndex = "-1"
      }, 2450);
      
    }

    video.muted = true;

    video.addEventListener('loadedmetadata', initCanvas, false);
    video.addEventListener('timeupdate', drawFrame, false);
    video.addEventListener('ended', onend, false);

    //video.src = URL.createObjectURL(document.querySelector('input').files[0]);
    video.src = myVideo;
    video.play();
  }

  scrollHandle = (deltaY) => {
    var {scrollIndex, scrollPos} = this.state;

    var scroll = scrollIndex + Math.round(deltaY * 0.05);

    scroll = Math.min(Math.max(0, scroll), array.length-1 + 50);


      this.setState({
        scrollIndex: scroll,
        scrollPos: Math.sign(deltaY)
      });

    console.log("ScrollIndex: " + Math.round(scrollIndex));
    console.log(scrollPos);

    var img = document.getElementById('image');

    var transformLoc = document.getElementById("main").style.transform;
    var transformLocInt = parseInt(transformLoc.substring(12,transformLoc.indexOf('v')));
    var currentOffset = transformLocInt + scrollIndex - array.length;

    if (scrollIndex >= 0 && scrollIndex < (array.length)) {
      img.src = URL.createObjectURL(array[scrollIndex]);

      document.getElementById("value").innerHTML = "";
      if (scrollIndex === 0 && scrollPos < 0) {
        console.log("YESSSS!");
        document.getElementById("value").innerHTML = 89;
      }
    } else {
      if (Math.round(scrollIndex) !== 0 && scrollPos > 0) {
        console.log("hey now! You're a rock star!");
        console.log(currentOffset);
        //document.getElementById("main").style.transform = "translateY(-" + currentOffset + "vh)";
        document.getElementById("value").innerHTML = currentOffset;
      }
    }
  }


  render() {
    return (
      <>
      <div id="titleVideo">

      </div>
      <p id="value" style={{display: 'none',}}></p>
      </>
    );
  }
}

export default App;
