/* ===================== Imports ==================== */
import React from 'react'; // import React to create React component
import './main.css'; // import the CSS file
/* ================================================== */

class Main extends React.Component {
    constructor() {
        super();
        this.state={
            scrollIndex: 0,
        }
        this.styles={
            spacer: {
                width: "100vw",
                height: "100vh"
            },
            title: {
                display: "flex",
                flexDirection: "column",
                alignItems: "center",
                width: "fit-content",
                margin: "0 auto",
                fontSize: "3.5vw",
                paddingTop: "15vh",
                borderBottom: "1px solid lightgray",
            },
            titleH1: {
                margin: "0",
            },
            titleH2: {
                padding: "1vh",
                margin: "0",
            },
            disc: {
                display: "flex",
                width: "-webkit-fill-available",
                justifyContent: "flex-end",
                marginBottom: "-1vh",
                fontSize: "4vw",
            },
            mainText: {
                width: "75%",
                margin: "0 auto",
                textAlign: "justify",
                textIndent: "5rem",
                /*paddingTop: "6vh",*/
                height: "inherit",
                boxShadow: "-2px 0px 10px whitesmoke, 2px 0px 10px whitesmoke",
                padding: "25px",
            },
        }
    }

    componentDidMount() {
        var main = document.querySelector('#main');
    
        main.addEventListener("wheel", event => {
        this.pageSliderControl(event.wheelDelta, event.deltaY);
        }, { passive: true});
    }

    componentWillUnmount() {
        var main = document.querySelector('#main');
    
        main.addEventListener("wheel", event => {
        this.pageSliderControl(event.wheelDelta, event.deltaY);
        }, { passive: true});
    }

    pageSliderControl = (wheelDelta, deltaY) => {

        var {scrollIndex} = this.state;

        var scroll = scrollIndex + Math.round(deltaY * 0.05);
    
        scroll = Math.min(Math.max(0, scroll), 300);

        var direction = Math.sign(deltaY);
    
        console.log("direction: " + direction);

        var newIndex = 0;
        
        if (direction > 0) {

            if ((scrollIndex >= 0 && scrollIndex <= 100) || scrollIndex > 150) {
                this.setState({
                    scrollIndex: scroll,
                });
            } else {
                
                newIndex = parseInt(document.getElementById("value").innerHTML);
                this.setState({
                    scrollIndex: newIndex,
                });
            }

        } else {
            if ((scrollIndex >= 0 && scrollIndex <= 90) || scrollIndex > 110) {
                this.setState({
                    scrollIndex: scroll,
                });

                if (scrollIndex > 110) {
                    document.getElementById("value").innerHTML = "";
                }
            } else {
                
                newIndex = parseInt(document.getElementById("value").innerHTML);
                this.setState({
                    scrollIndex: newIndex,
                });
            }
        }

        document.getElementById("main").style.transform = "translateY(-" + scrollIndex + "vh)";

        console.log("wheelDelta: " + wheelDelta + " | deltaY: " + deltaY + " | scrollIndex: " + scrollIndex);

        

    }

    render() {
        return(
            <>
                <div id="part1" style={this.styles.spacer}>
                    <div className="title" style={this.styles.title}>
                        <div className="disc" style={this.styles.disc}><h1 style={this.styles.titleH1}>DISCOVER</h1></div>
                        <h2 style={this.styles.titleH2}>WWI VIRTUAL PILGRIMAGE</h2>
                    </div>
                    <div className="mainText" style={this.styles.mainText}>
                        <p>
                            Welcome to WWI Virtual Pilgrimage, at here we are trying to find a new way to share and educate people about the stories of the soldiers who faught during WWI.
                        </p>
                    </div>
                </div>
                <div id="root"></div>
                <div id="part2" style={this.styles.spacer}></div>
            </>
        );
    }
}

export default Main;